#quote-box {
 display: flex;
 align-items: center;
 justify-content: flex-start;
 flex-direction: column;
 height: 100vh;
 background-color: gold;
 padding-top: 25vh;
}

.buttons {
display: flex;
justify-content: space-between;
margin-bottom: 25px;
width: 45%;
}

.qouteAndauthor {
 display: flex;
 margin-top: 30px;
 flex-direction: column;
 justify-content: space-evenly;
 background-color: white;
 color: black;
 padding: 15px;
 width: 45%;
 height: 45%;
 border-radius: 5px;
 text-align: center;
 font-size: 20px;
 font-style: italic;
}

.button {
 background-color: black;
 border: none;
 color: white;
 padding: 15px 32px;
 text-align: center;
 text-decoration: none;
 display: inline-block;
 font-size: 16px;
 cursor: pointer;
 border-radius: 5px;
}

.button:hover {
 background-color: white;
 color: black;
 box-shadow: 5px 5px black;
}

a:link {
 background-color: #1DA1F2;
 border-radius: 5px;
 text-decoration: none;
 width: 75px;
 text-align: center;
 padding: 15px 32px;
}

a:hover {
  background-color: #F5F8FA;
  box-shadow: 5px 5px black;
}

